.contact-main-div {
    display: flex;
    align-items: center;
    flex-direction: column;
    min-height: 90vh;
    padding-top: 3vw;
}

.contact-main-div h2 {
    align-self: center;
    font-family: 'L Regular';
    color: #8c857e;
    font-size: 1.8vw;
    margin-top: 5vw;
}

.contact-form {
    display: flex;
    width: 80vw;
    flex-direction: column;
}

.contact-form input {
    background-color: #f7f4ed;
    border: 3px solid #8c857e;
    height: 3vw;
    margin-bottom: 2vw;
    font-family: 'L Regular';
    font-size: 1vw;
}

.contact-form label {
    align-self: flex-start;
    color: #8c857e;
    font-family: 'L Regular';
    font-size: 1.5vw;
}

.contact-buttons-div {
    align-self: flex-start;
    display: flex;
    justify-content: center;
    width: 80vw;
    margin-top: 10vw;
    margin-bottom: 5vw;
    align-self: center;
}

.contact-continue-button {
    border: .18vw solid #8c857e !important;
    display: flex !important;
    color: #8c857e !important;
    background-color: transparent !important;
    cursor: pointer;
    align-items: center !important;
    justify-content: center;
    font-family: 'L Regular' !important;
    font-weight: 500;
    font-size: 1.9vw !important;
    width: 10.5vw;
    height: 3.5vw !important;
}

.contact-textarea {
    background-color: #f7f4ed;
    border: 3px solid #8c857e;
    height: 10vw;
    margin-bottom: 2vw;
    font-family: 'L Regular';
    font-size: 1vw;
}

.contact-modal-p {
    font-family: 'L Regular';
    color: #8c857e;
    font-size: 1.5vw;
    margin-top: 1.5vw;
}

.contact-modal-span {
    font-family: 'L Bold';
}

.normal-contact-modal div:nth-child(2) div:nth-child(3) {
    display: inline-block;
    border: none;
}

.normal-contact-modal div:nth-child(2) div:nth-child(3) button:nth-child(2) {
    display: none;
}

.normal-contact-modal div:nth-child(2) div:nth-child(3) button:nth-child(1) {
    font-family: 'L Regular';
    color: #8c857e;
    margin-top: -2vw;
    border: 2px solid #8c857e;
    font-size: 1.5vw;
    display: flex;
    align-items: center;
}

@media only screen and (max-width: 480px) {
    .contact-main-div {
        display: flex;
        align-items: center;
        flex-direction: column;
        min-height: 80vh;
        padding-top: 5vw;
    }
    .contact-main-div h2 {
        align-self: flex-start;
        margin-left: 10vw;
        font-family: 'L Regular';
        color: #8c857e;
        font-size: 5vw;
        text-align: start;
        margin-bottom: 5vw;
    }
    .contact-form {
        display: flex;
        width: 80vw;
        flex-direction: column;
    }
    .contact-form input {
        background-color: #f7f4ed;
        border: 3px solid #8c857e;
        height: 10vw;
        margin-bottom: 2vw;
        font-family: 'L Regular';
        font-size: 4vw;
    }
    .contact-form label {
        align-self: flex-start;
        color: #8c857e;
        font-family: 'L Regular';
        font-size: 4.5vw;
    }
    .contact-buttons-div {
        align-self: flex-start;
        display: flex;
        justify-content: center;
        width: 80vw;
        margin-top: 15vw;
        margin-bottom: 4vw;
        align-self: center;
    }
    .contact-continue-button {
        border: 2px solid #8c857e !important;
        display: flex !important;
        color: #8c857e !important;
        background-color: transparent !important;
        cursor: pointer;
        align-items: center !important;
        justify-content: center;
        font-family: 'L Regular' !important;
        font-weight: 500;
        font-size: 5vw !important;
        width: 20vw;
        height: 10vw !important;
    }
    .contact-textarea {
        background-color: #f7f4ed;
        border: 3px solid #8c857e;
        height: 20vw;
        margin-bottom: 2vw;
        font-family: 'L Regular';
        font-size: 4vw;
    }
    .contact-modal-p {
        font-family: 'L Regular';
        color: #8c857e;
        font-size: 4vw;
        margin-top: 5vw;
    }
    .contact-modal-span {
        font-family: 'L Bold';
    }
    .normal-contact-modal div:nth-child(2) div:nth-child(3) {
        display: inline-block;
        border: none;
    }
    .normal-contact-modal div:nth-child(2) div:nth-child(3) button:nth-child(2) {
        display: none;
    }
    .normal-contact-modal div:nth-child(2) div:nth-child(3) button:nth-child(1) {
        font-family: 'L Regular';
        color: #8c857e;
        margin-top: -2vw;
        border: 2px solid #8c857e;
        font-size: 4vw;
        display: flex;
        align-items: center;
    }
}